import React, { Component } from 'react'
import PropTypes            from 'prop-types'
//import CatItemCConn from '../containers/CatItemCConn'

class CatGroupC extends Component {
  static defaultProps = { catGroupData:{componentsText:'Placeholder', width:256, height:256, modPrefix:''} }
  static propTypes    = { catGroupData:PropTypes.shape({componentsText:PropTypes.string.isRequired, width:PropTypes.number.isRequired, height:PropTypes.number.isRequired}).isRequired, setModPrefix:PropTypes.func.isRequired }
  static baseCSSRules = {
    '.CatGroupClassOne':'{\nposition:absolute;\nfont-size:1em;\nline-height:1.2em;\ntop:1em;\nleft:1em;\nline-height:1.2em;\nheight:3em;\nmargin:0;\npadding:2px;\nborder:1px solid rgba(0,0,0,1);\nbackground-color: rgba(220,220,220,0.8);\nz-index:0;\ntext-align:left;\n}\n',
    '.CatGroupClassTwo':'{\nfont-size:1em;\nline-height:1.2em;\n;border:2px solid red;\nposition:relative;\npadding:4px;\nmargin:2px;\nmargin-top:50px;\n}\n'
  }
  static genNewModPrefix() {
    return 'CatGroupC_'+Date.now().toString(16).slice(4)+'_CatGroupC_';
  }

  instCSSRules={}
  reGenerateCSSRules = () => {let result=null,newRuleIdx=0,style=document.createElement('style');style.appendChild(document.createTextNode(''));document.head.appendChild(style);let stSh=style.sheet;for (let i in CatGroupC.baseCSSRules) {if (!CatGroupC.baseCSSRules.hasOwnProperty(i)) {continue;}let clSelExploded = i.split(' ');clSelExploded.forEach((currVal,idx,array)=>{clSelExploded[idx]=currVal.slice(0,1)+this.props.catGroupData.modPrefix+currVal.slice(1);});let newClSel = clSelExploded.join(' ');newRuleIdx = stSh.insertRule( (newClSel+'\n'+CatGroupC.baseCSSRules[i]), stSh.cssRules.length );result = stSh.cssRules[newRuleIdx];this.instCSSRules[i] = {'newSel':newClSel,'rule':result};}}

  /*Genereate and apply unique CSS rules for this component:*/
  componentWillMount = function() {this.reGenerateCSSRules();}
  componentDidMount  = function() { }

  render() {
    console.log('CatGroup:|-------------------------------------');
    console.log('CatGroup:| I am ALIVE!');
    console.log('CatGroup:| catGroupData:');
    console.log(this.props.catGroupData);
    console.log('CatGroup:| props:');
    console.log(this.props);
//    console.log('CatGroup:| Instantiating three Catalogue Items Components, providing them with the state data. The first one gets the following object:');
//    console.log(this.props.catGroupData.catItemDatas[0]);
    console.log('CatGroup:|-------------------------------------');

    return (
<div>
<hr/>
<h2>Catalogue Group "{this.props.catGroupData.short_name}"</h2>
<h6>modPrefix: "{this.props.catGroupData.modPrefix}"</h6>
{/*             <CatItemCConn catItemData={this.props.catGroupData.catItemDatas[0]} />
             <CatItemCConn catItemData={this.props.catGroupData.catItemDatas[1]} />
             <CatItemCConn catItemData={this.props.catGroupData.catItemDatas[2]} />  */}
</div>
    );
  }
}

export default CatGroupC



